@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
*{
  margin: 0%;
  padding: 0%;
}


*{box-sizing:border-box;margin:0;padding:0;font-family:"Poppins",sans-serif}body{margin:0.4rem auto;max-width:1296px;color:#282c34;background-color:#D3CDCC;padding:14px}li{list-style-type:none}a{text-decoration:none;color:#282c34;cursor:pointer}a:visited{color:#282c34}.navigation{margin:26px 0;display:flex}.navigation a{margin:10px;padding:10px;border-radius:4px;position:relative}.navigation .nav-active::after{content:"";width:85%;height:5px;background:#61dafb;position:absolute;bottom:1px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.logo{display:flex;position:absolute;top:5px;left:50%;-webkit-transform:translate(-50%);transform:translate(-50%);background:url(/static/media/main.516fbcd1.jpg) center/cover;height:60px;width:200px;align-items:center;justify-content:center;border-radius:10px;box-shadow:0px 1px 2px 0px rgba(51,51,51,0.5)}.logo h3{color:#D3CDCC;letter-spacing:1px}.logo img{height:34px;width:34px;margin-right:10px;object-fit:cover}.card{height:80px;position:relative;margin:5px;box-shadow:0 0 2px rgba(51,51,51,0.25);transition:0.3s;overflow:hidden;border-radius:4px}.card:hover{-webkit-transform:scale(1.25);transform:scale(1.25);z-index:2}.card img{object-fit:cover;height:80px;width:160px}.card .data-container{position:absolute;top:0;opacity:0;width:100%;height:100%;transition:0.2s}.card .data-container:hover{opacity:1}.card .data-container:hover ul{-webkit-transform:scaleY(1);transform:scaleY(1)}.card .data-container ul{color:#D3CDCC;padding-top:5px;display:grid;height:100%;width:100%;text-align:center;transition:0.3s;-webkit-transform:scaleY(0.3);transform:scaleY(0.3);z-index:20}.card .data-container ul:after{content:"";width:100%;height:100%;position:absolute;top:0;left:0;transition:0.02s}.card .data-container ul:hover::after{background:rgba(0,0,0,0.65)}.card .data-container li{font-size:0.8rem;z-index:30}.card .data-container li:nth-child(1){font-weight:bold;color:#61dafb}.countries .sort-container{display:grid;grid-template-columns:20% 80%;padding:8px;border-radius:15px;background:#282c34;color:#61dafb;box-shadow:0 2px 2px rgba(51,51,51,0.3)}.countries .sort-container ul{display:flex;justify-content:space-around}.countries .sort-container ul input[type="radio"]{-webkit-transform:translate(-5px, 1px);transform:translate(-5px, 1px)}.countries .cancel{margin:20px auto 30px;width:100%}.countries .cancel h5{border:1px #282c34 solid;text-align:center;width:180px;margin:0 auto;border-radius:10px;transition:0.3s;cursor:pointer}.countries .cancel h5:hover{background:#61dafb;border-color:#282c34}.countries .countries-list{display:flex;flex-wrap:wrap;margin:30px}.news-container h1{text-align:center;margin-bottom:10px}.news-container form{display:grid;min-width:400px;text-align:center;width:50%;max-width:600px;margin:0 auto}.news-container form input,.news-container form textarea{margin:5px 0;padding:5px;border-radius:5px;border:1px solid #61dafb;resize:none;box-shadow:0 2px 6px rgba(0,0,0,0.05)}.news-container form textarea{height:80px}.news-container form p{font-size:0.8rem;color:red}.news-container form input[type="submit"]{width:200px;margin-left:auto;cursor:pointer;transition:0.4s;background:#61dafb}.news-container form input[type="submit"]:hover{background:#282c34;color:white}.article{margin:10px 50px;background:white;box-shadow:0 2px 6px rgba(0,0,0,0.1);border-radius:5px;padding:15px}.article .card-header{display:flex;justify-content:space-between;margin:5px 0 10px}.article .card-header em{font-size:0.8rem}.article p{font-size:0.9rem}.article textarea{width:100%;resize:none;height:70px;outline:none}.article .btn-container{display:flex;justify-content:flex-end;align-items:center;margin:10px 0 5px}.article .btn-container button{width:70px;height:20px;font-size:0.7rem;margin:0 2px;cursor:pointer}.article .btn-container button:hover{-webkit-filter:brightness(110%);filter:brightness(110%)}

